.order-popup__popup-form {
  position: relative;
  display: flex;
  box-sizing: border-box;
  flex-direction: column;
  width: 320px;
  margin: auto;
  padding: 28.5px 30px 46px;
  background-color: $primary-background;
  border: 1px solid $light-border;
  box-shadow: 0 5px 10px rgba(0, 1, 1, 0.25);
  align-content: center;
}

.popup-form__popup-form-title {
  margin: 0;
  text-align: center;
  text-transform: uppercase;

  @include big-header;
}

.popup-form__add-to-cart-button {
  color: $hightlight-font;
  border: 2px solid $hightlight-border;
  background-color: $hightlight-background;
  font-size: 17px;
  line-height: 16px;
}
